:root {
  --white: #fff;
  --gray: #eeefdf;
  --black: #000;
  --darkgray: #1C1B1B;
  --red: #e43323;
  --blue: #1a1d99;
  --yellow: #fcd46b;
  --fill: var(--yellow);
  --settings-bg: var(--white); }

html {
  box-sizing: border-box; }

*,
*:before,
*:after {
  box-sizing: inherit; }

body {
  margin: 0;
  overflow: hidden;
  font-family: Arial, Helvetica, sans-serif; }

.main {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(80px, 1fr));
  grid-auto-rows: 80px;
  grid-auto-flow: dense;
  grid-gap: 1vw;
  margin: 0;
  padding: 0;
  list-style: none;
  background: var(--black);
  width: 100vw;
  height: 100vh; }

.main li {
  position: relative;
  transition: all 0.3s ease-in-out; }

.main li::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-image: url("/public/images/dark-grey-terrazzo.png");
  /* background-size: 100px; */
  opacity: 0.1;
  mix-blend-mode: exclusion; }

.settings {
  position: fixed;
  width: 400px;
  background: var(--settings-bg);
  top: 0;
  right: -400px;
  padding: 10px;
  font-size: 0.8rem; }
  .settings table {
    width: 100%;
    margin: 0 0 2rem; }
  .settings label {
    white-space: nowrap; }
  .settings input[type="text"],
  .settings input[type="number"] {
    width: 100%; }

.settings:hover,
.settings:focus {
  right: 0; }

.settings::before {
  content: "Settings";
  display: inline-block;
  position: absolute;
  top: 5px;
  right: 100%;
  padding: 5px;
  background: var(--settings-bg); }

.generate-btns {
  text-align: right; }
